import React from 'react'
import emailjs from 'emailjs-com';
import "../css/Feedback.css"
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
toast.configure()


function Feedback() {

  function sendEmail(e) {
    e.preventDefault();

    
    emailjs.sendForm('service_ajlz4sc', 'template_xwz4n88', e.target, 'user_vqBOKTsWcUqgz8j7P3JRq')
      .then((result) => {
        document.getElementById('nameClass').value = '';
        document.getElementById('emailClass').value = '';
        document.getElementById('msgClass').value = '';
        toast('Thanks for your valuable feedback',{position: toast.POSITION.TOP_RIGHT}) 
       
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  }

  return (

    <div>
      
         <form className="feed-form" onSubmit={sendEmail}>
      
      
      <input id="nameClass" className="inputId" type="text" placeholder="Name" name="name" />
      
      
      <input id="emailClass" className="inputId" type="email" placeholder="Email" name="email" />
      <small id="emailHelp" >We'll never share your email with anyone else.</small>

      <textarea id="msgClass" className="inputId" name="message" placeholder="Your feedback here"></textarea>
      
      <input id="submitId" type="submit" value="Submit" />
    </form>
    </div>
   
  );
}
export default Feedback