import React, { useEffect } from 'react'

import firebase from '../utils/firebase';
import { Modal,Button } from 'react-bootstrap';
import { useState } from 'react';
import '../css/Create2.css'
import img from "../images/lock2.png"
import logoImg from "../images/albatross-logo.svg"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCopy, faGrinHearts, faGrinStars, faHeart,faShare} from '@fortawesome/free-solid-svg-icons'
import { shareTextToWhatsApp } from 'share-text-to-whatsapp';

function Create2(){
    const [show, setShow] = useState(false);
    const [requestVid, setrequestVid] = useState(true);
    const [url, setUrl] = useState("Url will appear here");
    const [deltime, setDelTime] = useState("00:00:00");
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
  
  
    function submitInfo(e) {
      e.preventDefault()
      console.log("Document",document)
      var msg = document.getElementById("msgId").value.trim()
      var name = document.getElementById("fromId").value.trim()
      var email = document.getElementById("emailId").value.trim()
      // var que = document.getElementById("queId").value.trim()
      // var ans = document.getElementById("ansId").value.trim()
      
      {/* SUBMIT TO FIREBASE */}
      const msgsRef = firebase.database().ref('Messages/')
      const end = Date.now()+(30*60*1000)
      var dateFormat = require("dateformat");
      var s = dateFormat(end, "dddd, mmmm dS, yyyy, h:MM:ss TT");
    setDelTime(s)
      const newMsg = {
        MSG:msg,
        C:end,//TODO: LATER UPDATE TO 2=>30
        F:name,
        M:email,
        S: false,
        R:requestVid?"T":"F", //REQUEST VIDEO
      //   que:que,
      //   ans:ans
      };
      var newKey = msgsRef.push().key;
      const msgsRef2 = firebase.database().ref('Messages/'+newKey)
      const intRef = firebase.database().ref('Interested')
      msgsRef2.set(newMsg)
      setUrl(newKey)
      handleShow()
      document.getElementById("msgId").value = ""
      document.getElementById("fromId").value=""
      document.getElementById("emailId").value=""
      console.log("NEW KEY",newKey)
  
      intRef.push({"mail":email})
      
  }
  function copyUrl() {
    /* Get the text field */
    var copyText = document.getElementById("urlId");
  
    /* Select the text field */
    copyText.select();
    copyText.setSelectionRange(0, 99999); /* For mobile devices */
  
    /* Copy the text inside the text field */
    document.execCommand("copy");
  
    /* Alert the copied text */
    console.log("Copied the text: " + copyText.value);
  } 
  function shareViaWhatsapp(){
    const msg = "I have sent you a secret message. Go rush to unveil it as it will be deleted by "+ deltime
    shareTextToWhatsApp(msg)
  }
  function handleCheckChange(event){
    setrequestVid(!requestVid)
  
  }
  
     
    return(
      <div className="mother-div">
      
         <div className="second-div">
         <div className="c11">
            <div className="create-column">
              <h3 id="create-txt">Create Message Box</h3>
              <div className="create-head">
                <img className="create-head-img" src={img}/>
                <p className="create-head-txt" >This seems to be a start of something fabulous</p>
              </div>
             <form className="form-lyt"  onSubmit={submitInfo}>
             <textarea className="msg-txt" id="msgId" placeholder="Message" required/>
              <input type="text" className="name-txt" id="fromId" placeholder="Name" required/>
              
              <input type="text" id="emailId" className="email-txt" placeholder="Email" required/>
              
              
              
                <div class="checkbox-lyt">
                  <div class="input-group-text">
                    <input className="checkbox-box" type="checkbox" onChange={handleCheckChange} checked={requestVid}  aria-label="Checkbox for following text input"/>
                    
                    <small className="small-txt small-txt2">Request for reaction video</small>
                  </div>
                </div>
               
            
              <button type="submit" className="create-btn">CREATE</button>

             </form>     
                           <small className="small-txt left-txt"><b>Note: </b>Reaction video will be sent to the above email id</small>
       
            </div>
          </div>
          <div className="c22">
          <p className="create-head-txt feedback-head-txt" >What our users say?</p>
          <div className="feedback-card">
            <div className="feedback-img-div">
            <img src={img} className="feedback-img"/>
            </div>
            <div className="feedback-body">
              <p className="feedback-name">Mahesh Raval</p>
              <p className="feedback-feedback">Had a fabulous experience with this application</p>
              
            </div>

          </div>
          <div className="feedback-card">
            <div className="feedback-img-div">
            <img src={img} className="feedback-img"/>
            </div>
            <div className="feedback-body">
              <p className="feedback-name">Mahesh Raval</p>
              <p className="feedback-feedback">Had a fabulous experience with this application</p>
              
            </div>

          </div>
          <p className="many-more-txt"> and many more</p>
          <section className="section-four">
            <div className="from-class">
              <h4 className="from-txt">from</h4>
              <img className="from-logo" src={logoImg}/>
            </div>
          </section>
          </div>
         </div>
         <Modal  backdrop="static" show={show} onHide={handleClose}>
          <Modal.Header >
          <Modal.Title><p className="modal-title">Made one for you, with Love <FontAwesomeIcon style={{color:"red"}} size='1x' icon={faHeart} />  <br/> Thanks for using</p> </Modal.Title>

          </Modal.Header>
          <Modal.Body>
          <small className="modal-desc">{"Share this link as fast as you can as the message will be deleted by "}<strong>{deltime}</strong></small>
          <br/>
          <input type="text" value={"https://albatrosscreations.github.io/Wowio/#/home/"+url} id="urlId"/>
          <Button style={{marginLeft:"20px"}} variant="primary" onClick={copyUrl}> <FontAwesomeIcon style={{color:"white"}} size='1x' icon={faCopy} /></Button>
          <Button style={{marginLeft:"20px"}} variant="primary" onClick={shareViaWhatsapp}> <FontAwesomeIcon style={{color:"white"}} size='1x' icon={faShare} /></Button>


          </Modal.Body>
          <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>Close</Button>

          

          
          </Modal.Footer>
        </Modal>
      </div>

    )

}
export default Create2