
import '../css/Game.css';
import { Context, SnakeGame } from "react-game-snake";
import {React,useState} from 'react'
import { propTypes } from 'react-bootstrap/esm/Image';
import { Prompt } from 'react-router';
import { Component } from 'react';

import upImg from '../images/top.svg'
import downImg from '../images/bottom.svg'
import leftImg from '../images/left.svg'
import rightImg from '../images/right.svg'

class GameComponent extends Component{

  constructor(props){
    super(props)
    this.state={
      score:0,
      width: window.innerWidth,
    }
    this.handleWindowSizeChange=this.handleWindowSizeChange.bind()
  }
  



  componentDidMount(){
    document.body.style.backgroundColor = "#000000"

  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }
  
  // make sure to remove the listener
  // when the component is not mounted anymore
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }
  
  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  
   Completionist = () => <span>You are good to go!</span>;

    // Renderer callback with condition
     renderer = ({ hours, minutes, seconds, completed }) => {
      if (completed) {
        // Render a completed state
        return this.Completionist;
      } else {
        // Render a countdown
        return <span>{hours}:{minutes}:{seconds}</span>;
      }
    };

  

  render(){

    function simulateKey (keyCode, type, modifiers) {
      var evtName = (typeof(type) === "string") ? "key" + type : "keydown";	
      var modifier = (typeof(modifiers) === "object") ? modifier : {};
    
      var event = document.createEvent("HTMLEvents");
      event.initEvent(evtName, true, false);
      event.keyCode = keyCode;
    
    
      document.dispatchEvent(event);
    }
    function goUp(){
      
      simulateKey(38)
      console.log("UP")
  
    }
    function goDown(){
      simulateKey(40)
      console.log("DOWN")
  
    }
    function goLeft(){
      simulateKey(37)
      console.log("LEFT")
  
    }
    function goRight(){
      simulateKey(39)
      console.log("RIGHT")
  
    }
      return(
        <div className="mother-container">
          <p className="game-title-txt">Feed the snake twice before the time hits 00:00 to win. All the best ;)</p>
          <div className="container1">
          <section className="section-11">
          <SnakeGame id="snake-comp"
             colors={{
                 field: "#FFFFFF",
                 food: "#539400",
                 snake: "#FF5C00",
             }}
             countOfHorizontalFields={20}
             countOfVerticalFields={20}
             fieldSize={10}
             loopTime={200}
             pauseAllowed={false}
             restartAllowed={true}
             
             // onLoose={(context: Context) => alert(`You loosed with ${context.game.points} points.`)}
             // onPause={(context: Context) => alert("paused")}
             // onRestart={(context: Context) => alert("restarted")}
             // onResume={(context: Context) => alert("onResume")}
             // onWin={(context: Context) => alert(`You won with ${context.game.points} points.`)}
             onLoopFinish={(context: Context) => {
               if(context.game.points>0){
                 console.log("You won ")
                 this.props.onWin()
               }
             }}
         ></SnakeGame>
          </section>
          <section className="section-22">
             <div className="vert-btns">
               <img className="btn-img btn-top" onClick={goUp} src={upImg}/>
             <div className="horiz-btns">
             <img className="btn-img btn-left" onClick={goLeft} src={leftImg}/>
             <img className="btn-img btn-left" onClick={goRight} src={rightImg}/>
             </div>
             <img className="btn-img btn-top" onClick={goDown} src={downImg}/>
             </div>
          </section>
          </div>
          <p className="game-title-txt">Note: If you are on desktop you can play with arrow keys ;)</p>
        </div>
       )
              
    
  }
}

export default GameComponent;
